import request from '../utils/request'

// 历史分析API
export function getOrderTrend(params: { time_range?: string; granularity?: string }) {
  return request({ url: '/api/v1/admin/monitor/analysis/order-trend', method: 'get', params })
}

export function getConversionAnalysis(params: { time_range?: string; granularity?: string }) {
  return request({ url: '/api/v1/admin/monitor/analysis/conversion', method: 'get', params })
}

export function getAnomalyPatterns(params: { time_range?: string }) {
  return request({ url: '/api/v1/admin/monitor/analysis/anomalies', method: 'get', params })
}

export function getPerformanceMetrics(params: { time_range?: string }) {
  return request({ url: '/api/v1/admin/monitor/analysis/performance', method: 'get', params })
}




