<template>
  <div class="analysis-page">
    <h2 class="page-title">历史数据分析</h2>

    <el-card class="mb16" shadow="never">
      <el-form :inline="true" @submit.prevent>
        <el-form-item label="时间范围">
          <el-select v-model="timeRange" style="width: 140px">
            <el-option label="1小时" value="1h" />
            <el-option label="6小时" value="6h" />
            <el-option label="24小时" value="24h" />
            <el-option label="7天" value="7d" />
            <el-option label="30天" value="30d" />
          </el-select>
        </el-form-item>
        <el-form-item label="粒度">
          <el-select v-model="granularity" style="width: 140px">
            <el-option label="按小时" value="hour" />
            <el-option label="按天" value="day" />
          </el-select>
        </el-form-item>
        <el-button type="primary" :loading="loading" @click="loadAll">刷新</el-button>
      </el-form>
    </el-card>

    <el-row :gutter="16">
      <el-col :span="12">
        <el-card class="mb16" shadow="never">
          <template #header>订单趋势</template>
          <div class="chart-box">
            <div ref="orderTrendChartRef" class="chart"></div>
          </div>
        </el-card>
      </el-col>
      <el-col :span="12">
        <el-card class="mb16" shadow="never">
          <template #header>支付转化率</template>
          <div class="chart-box">
            <div ref="conversionChartRef" class="chart"></div>
          </div>
        </el-card>
      </el-col>
    </el-row>

    <el-row :gutter="16">
      <el-col :span="12">
        <el-card class="mb16" shadow="never">
          <template #header>异常模式识别</template>
          <div>
            <h4 class="sub">错误最多节点TOP10</h4>
            <el-table :data="anomalies.top_error_nodes" size="small" v-loading="loading">
              <el-table-column prop="node" label="节点" min-width="220" />
              <el-table-column prop="error_count" label="错误数" width="100" />
            </el-table>
            <h4 class="sub mt12">常见错误消息TOP20</h4>
            <el-table :data="anomalies.frequent_error_messages" size="small">
              <el-table-column prop="message" label="消息" min-width="260" />
              <el-table-column prop="count" label="次数" width="100" />
            </el-table>
            <h4 class="sub mt12">错误尖峰</h4>
            <el-table :data="anomalies.error_spikes" size="small">
              <el-table-column prop="time" label="时间" width="180" />
              <el-table-column prop="count" label="错误数" width="100" />
            </el-table>
          </div>
        </el-card>
      </el-col>
      <el-col :span="12">
        <el-card class="mb16" shadow="never">
          <template #header>性能指标分析</template>
          <div>
            <el-descriptions :column="3" border size="small">
              <el-descriptions-item label="样本数">{{ performance.response_time.count }}</el-descriptions-item>
              <el-descriptions-item label="平均">{{ performance.response_time.avg }} ms</el-descriptions-item>
              <el-descriptions-item label="最小">{{ performance.response_time.min }} ms</el-descriptions-item>
              <el-descriptions-item label="最大">{{ performance.response_time.max }} ms</el-descriptions-item>
              <el-descriptions-item label="P95">{{ performance.response_time.p95 }} ms</el-descriptions-item>
              <el-descriptions-item label="P99">{{ performance.response_time.p99 }} ms</el-descriptions-item>
            </el-descriptions>
            <h4 class="sub mt12">最慢调用TOP20</h4>
            <el-table :data="performance.slow_calls" size="small">
              <el-table-column prop="created_at" label="时间" width="180" />
              <el-table-column prop="node" label="节点" min-width="220" />
              <el-table-column prop="trace_id" label="TraceID" min-width="180" />
              <el-table-column prop="platform_order_no" label="平台单号" min-width="160" />
              <el-table-column prop="duration" label="耗时(ms)" width="120" />
            </el-table>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script setup lang="ts">
import { ref, reactive, onMounted, nextTick } from 'vue'
import { ElMessage } from 'element-plus'
import { getOrderTrend, getConversionAnalysis, getAnomalyPatterns, getPerformanceMetrics } from '@/api/monitor-analysis'

const timeRange = ref<'1h'|'6h'|'24h'|'7d'|'30d'>('24h')
const granularity = ref<'hour'|'day'>('hour')
const loading = ref(false)

const orderTrend = ref<any[]>([])
const conversion = ref<any[]>([])
const anomalies = reactive({
  top_error_nodes: [] as any[],
  frequent_error_messages: [] as any[],
  error_spikes: [] as any[]
})
const performance = reactive({
  response_time: { count: 0, avg: 0, min: 0, max: 0, p95: 0, p99: 0 },
  slow_calls: [] as any[],
  throughput: [] as any[]
})

async function loadAll() {
  loading.value = true
  try {
    const [trendRes, convRes, anoRes, perfRes] = await Promise.all([
      getOrderTrend({ time_range: timeRange.value, granularity: granularity.value }),
      getConversionAnalysis({ time_range: timeRange.value, granularity: granularity.value }),
      getAnomalyPatterns({ time_range: timeRange.value }),
      getPerformanceMetrics({ time_range: timeRange.value })
    ])
    
    console.log('趋势数据响应:', trendRes)
    console.log('转化率数据响应:', convRes)
    console.log('异常模式数据响应:', anoRes)
    console.log('性能指标数据响应:', perfRes)
    
    // 处理订单趋势数据
    if (((trendRes as any).code === 0 || (trendRes as any).code === 200) && (trendRes as any).data?.trend) {
      orderTrend.value = (trendRes as any).data.trend
    } else {
      orderTrend.value = []
    }
    
    // 处理转化率数据
    if (((convRes as any).code === 0 || (convRes as any).code === 200) && Array.isArray((convRes as any).data)) {
      conversion.value = (convRes as any).data
    } else {
      conversion.value = []
    }
    
    // 处理异常模式数据
    if (((anoRes as any).code === 0 || (anoRes as any).code === 200) && (anoRes as any).data) {
      anomalies.top_error_nodes = (anoRes as any).data.top_error_nodes || []
      anomalies.frequent_error_messages = (anoRes as any).data.frequent_error_messages || []
      anomalies.error_spikes = (anoRes as any).data.error_spikes || []
    } else {
      anomalies.top_error_nodes = []
      anomalies.frequent_error_messages = []
      anomalies.error_spikes = []
    }
    
    // 处理性能指标数据
    if (((perfRes as any).code === 0 || (perfRes as any).code === 200) && (perfRes as any).data) {
      performance.response_time = (perfRes as any).data.response_time || performance.response_time
      performance.slow_calls = (perfRes as any).data.slow_calls || []
      performance.throughput = (perfRes as any).data.throughput || []
    } else {
      performance.response_time = { count: 0, avg: 0, min: 0, max: 0, p95: 0, p99: 0 }
      performance.slow_calls = []
      performance.throughput = []
    }
  } catch (e: any) {
    console.error('加载失败:', e)
    ElMessage.error(e?.message || '加载失败')
  } finally {
    loading.value = false
    await nextTick()
    await ensureEcharts()
    renderOrderTrend()
    renderConversion()
  }
}

onMounted(loadAll)

// ECharts 按需加载（CDN）
const echartsLoaded = ref(false)
function ensureEcharts(): Promise<void> {
  return new Promise((resolve) => {
    if ((window as any).echarts) { echartsLoaded.value = true; return resolve() }
    const s = document.createElement('script')
    s.src = 'https://cdn.jsdelivr.net/npm/echarts@5/dist/echarts.min.js'
    s.onload = () => { echartsLoaded.value = true; resolve() }
    document.head.appendChild(s)
  })
}

// 图表渲染
const orderTrendChartRef = ref<HTMLDivElement | null>(null)
const conversionChartRef = ref<HTMLDivElement | null>(null)
let orderTrendChart: any = null
let conversionChart: any = null

function renderOrderTrend() {
  if (!echartsLoaded.value || !orderTrendChartRef.value) return
  const echarts = (window as any).echarts
  orderTrendChart = orderTrendChart || echarts.init(orderTrendChartRef.value)
  const x = orderTrend.value.map(d => d.time)
  const y = orderTrend.value.map(d => d.count)
  orderTrendChart.setOption({
    tooltip: { trigger: 'axis' },
    grid: { left: 40, right: 20, top: 30, bottom: 40 },
    xAxis: { type: 'category', data: x, axisLabel: { rotate: 45 } },
    yAxis: { type: 'value', name: '订单量' },
    series: [{ name: '订单量', type: 'line', smooth: true, data: y, areaStyle: {} }]
  })
}

function renderConversion() {
  if (!echartsLoaded.value || !conversionChartRef.value) return
  const echarts = (window as any).echarts
  conversionChart = conversionChart || echarts.init(conversionChartRef.value)
  const x = conversion.value.map(d => d.time)
  const total = conversion.value.map(d => d.total ?? d.total_orders ?? d.total_count ?? 0)
  const paid = conversion.value.map(d => d.paid ?? d.paid_orders ?? 0)
  const rate = conversion.value.map(d => d.conversion_rate ?? 0)
  conversionChart.setOption({
    tooltip: { trigger: 'axis' },
    grid: { left: 40, right: 40, top: 30, bottom: 40 },
    legend: { data: ['总订单', '已支付', '转化率(%)'] },
    xAxis: { type: 'category', data: x, axisLabel: { rotate: 45 } },
    yAxis: [
      { type: 'value', name: '订单数' },
      { type: 'value', name: '转化率(%)', min: 0, max: 100 }
    ],
    series: [
      { name: '总订单', type: 'bar', data: total, yAxisIndex: 0 },
      { name: '已支付', type: 'bar', data: paid, yAxisIndex: 0 },
      { name: '转化率(%)', type: 'line', data: rate, yAxisIndex: 1, smooth: true }
    ]
  })
}
</script>

<style scoped>
.analysis-page { padding: 16px; }
.page-title { margin: 0 0 12px; }
.mb16 { margin-bottom: 16px; }
.mt12 { margin-top: 12px; }
.sub { margin: 8px 0; }
.chart-box { min-height: 160px; background: #fafafa; border: 1px solid #f0f0f0; border-radius: 6px; padding: 8px; }
.mono { white-space: pre-wrap; word-break: break-all; font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", monospace; font-size: 12px; color: #606266; }
.chart { width: 100%; height: 300px; }
</style>
